@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@400;800&family=Open+Sans:wght@300;400;700&family=Source+Code+Pro&display=swap);
/* home.css
 * styling for the home/landing page
*/

.main-title {
  font-family: Nunito;
  font-style: normal;
  font-weight: 800;
  font-size: 56px;
  line-height: 56px;
  text-align: center;
  letter-spacing: 0.305em;
  color: var(--dark-purple);
  margin-top: 12px;
  z-index: 30;
  position: relative;
  display: block;
}
.sub-title {
  margin-top: 12px;
  position: relative;
  font-family: "Source Code Pro", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 21px;
  line-height: 35px;
  text-align: center;
  color: var(--dark-purple);
  z-index: 30 !important;
}
.top-container {
  position: fixed;
  width: 100vw;
  margin-top: 54px;
  z-index: 1000;
}

/* static.css
 * styling for static pages
*/

.static {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.static p {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  margin: 10px 40px;
  max-width: 720px;
  line-height: 1.5em;
  text-align: justify;
}
.static img {
  display: block;
  width: 400px;
  margin: 20px auto;
  border: 2px solid var(--dark-purple);
  border-radius: 5px;
}
.static .center {
  text-align: center;
}
.static .caption {
  font-size: 15px;
  margin-bottom: 20px;
  margin-top: -7px;
}

/* practice.css
 * styling for the practice pages
*/

.practice-menu-container {
  display: block;
  margin-top: 100px;
  height: 79px;
}
.practice-menu-container > h1 {
  font-family: Open Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 34px;
  line-height: 34px;
  text-align: center;
  margin-bottom: 36px;

  color: var(--dark-purple);
}
.practice-menu-container div a {
  display: block;
  width: 300px;
  height: 205px;
  margin: 10px;

  background: #ffffff;
  border: 2px solid var(--dark-purple);
  box-sizing: border-box;
  border-radius: 16px;
  text-decoration: none;
  color: var(--dark-purple);
  transition: box-shadow 0.2s, transform 0.2s;
}
.practice-menu-container div a:hover {
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
  transform: scale(1.05);
}
.practice-menu-container div a:active {
  transform: scale(1);
}
.practice-menu-container div a h2 {
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 35px;
  text-align: center;
  margin-top: 30px;
}
.practice-menu-container div a p {
  text-align: center;
  margin: 20px 30px;
  line-height: 1.5;
}
.practice-menu-container div {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.p-container {
  display: flex;
  align-items: center;
  flex-direction: column;
}
.p-container p {
  display: block;
  text-align: center;
  font-family: "Open Sans", sans-serif;
  color: var(--dark-purple);
  margin: 20px;
  line-height: 1.4;
  max-width: 400px;
}
.practice-main {
  width: 100vw;
  overflow: hidden;
  height: 100vh;
}
.question {
  color: var(--dark-purple);
  font-family: "Source Code Pro", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 48px;
  height: 60px;
  white-space: nowrap;
  transition: transform 0.4s;
}
.question-string {
  color: var(--dark-purple);
  font-family: "Source Code Pro", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 48px;
  display: flex;
  height: 60px;
  justify-content: center;
}
.question-string .highlighted {
  background-color: #faff00;
  margin-left: 28.8px;
  margin-right: 28.8px;
}
.highlight {
  display: block;
  height: 60px;
  background-color: #faff00;
  position: absolute;
  left: 50vw;
  transform: translateX(-50%);
  transition: width 0.4s;
  width: 28.8px;
  border-radius: 5px;
}
.question-string .morse {
  margin: 0 20px;
}
.question-string .sides-left {
  width: calc((100vw - 28.798px) / 2 - 20px);
  text-align: right;
  text-overflow: clip;
  overflow: hidden;
  white-space: nowrap;
  direction: rtl;
}
.question-string .sides-right {
  width: calc((100vw - 28.798px) / 2 - 20px);
  text-overflow: clip;
  overflow: hidden;
  white-space: nowrap;
}
.question-string .english {
  width: calc((100vw - 28.798px) / 2);
}
.practice-container input {
  display: block;
  width: 300px;
  height: 40px;
  font-size: 40px;
  margin: 50px auto;
  transition: background-color 0.1s;
}
.practice-container .green-input {
  background-color: rgb(166, 250, 110);
}
.practice-container .red-input {
  background-color: rgb(250, 110, 110);
}

.back-link {
  display: block;
  margin: 0px 0 50px 50px;
  color: var(--dark-purple);
  text-decoration: none;
  font-family: Open Sans;
  font-size: 20px;
  transition: transform 0.5s;
  width: 400px;
}

.reveal-btn {
  cursor: pointer;
  color: var(--dark-purple);
  display: block;
  text-align: center;
  font-size: 20px;
  padding: 16px;
  width: 150px;
  border-radius: 10px;
  margin: 0 auto;
  transition: transform 0.2s;
  border: 1px solid var(--dark-purple);
  margin-top: 50px;
}
.reveal-btn:active {
  transform: translateY(2px);
}
.reveal-btn:hover {
  transform: scale(1.1);
}
.answer {
  text-align: center;
  font-size: 20px;
  padding: 16px;
  margin-top: 50px;
}
.left-arrow {
  width: 30px;
  transform: translateY(15px);
  margin-right: 20px;
}

.back-link:hover {
  transform: translateX(-20px);
}

/* nav.css
 * styling for the navigation bar
*/

nav {
  position: fixed;
  width: 100%;
  height: 72px;
  left: 0px;
  top: 0px;

  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  z-index: 2;
  transition: transform 0.5s, padding-top 0.5s, height 0.5s, box-shadow 0.5s;
  background-color: white;
}

nav ul {
  float: right;
}

nav > a {
  position: absolute;
  width: 200px;
  height: 36px;
  left: 32px;
  top: 15px;
  font-family: Nunito;
  font-style: normal;
  font-weight: 800;
  font-size: 36px;
  line-height: 36px;
  text-decoration: none;
  letter-spacing: 0.305em;

  color: var(--dark-purple);
}

nav ul li {
  display: inline-block;
  width: 100px;
  height: 79px;
  margin-top: 25px;
  text-align: center;
}

nav ul li a {
  font-family: Open Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 20px;
  text-align: center;
  text-decoration: none;
  width: 100%;
  height: 100%;
  display: block;
  transition: transform 0.2s;

  color: var(--dark-purple);
}

nav ul li a:hover {
  transform: scale(1.1);
}

nav ul li a p {
  font-family: "Open Sans", sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  margin-top: 11px;
}

/* indicator bar for current page */
.activeNavLink::after {
  content: "";
  display: block;
  width: 80px;
  height: 4px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 23px;

  background-color: var(--dark-purple);
  border-radius: 27px 27px 0px 0px;
  transition: display 2s;
}

/* transitions.css
 * styling for transitions between pages of the website
*/

/* before entering */
.fade-enter {
  position: absolute;
  opacity: 0;
}
/* after entering */
.fade-enter-active {
  position: absolute;
  opacity: 1;
  /* 500ms value is set in App.js */
  transition: opacity 500ms ease-in-out;
}
/* before exiting */
.fade-exit {
  position: absolute;
  opacity: 1;
}
/* after exiting */
.fade-exit-active {
  position: absolute;
  opacity: 0;
  /* 500ms value is set in App.js */
  transition: opacity 500ms ease-in-out;
}

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* main.css
 * basic styling for the whole website
*/

/* import google fonts */

/* colors */
:root {
  --white: #ffffff;
  --dark-purple: #34344a;
}

/* some default styles */
body {
  font-family: "Open Sans", sans-serif;
  color: #34344a;
  color: var(--dark-purple);
  height: 100vh;
  background-color: #ffffff;
  background-color: var(--white);
  background-size: 100% auto;
}

.container {
  margin-top: 56px;
  width: 100vw;
  position: absolute;
}

h1 {
  font-family: Open Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 34px;
  line-height: 34px;
  text-align: center;
  margin-bottom: 18px;

  color: #34344a;

  color: var(--dark-purple);
}

